.servicesMother {
  height: 100vh;
  .servicesMain {
    display: flex;
    flex-direction: row;
    height: 100%;

    .leftSect {
      height: 100vh;
      width: 50%;
      background: #4698fe;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding-right: 30px;
      .headDiv {
        margin-bottom: 100px;
        display: flex;
        justify-content: flex-end;
        width: 100%;
        .head {
          text-align: end;
          color: #333;
          font-size: 4rem;
        }
      }
    }

    .rightSect {
      height: 100%;
      width: 50%;
      background: #333;
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex-direction: column;
      .rightSectChild {
        padding-left: 30px;
        display: flex;
        .servicesGrid {
          display: grid;
          grid-template-columns: 100%;
          grid-template-rows: auto auto;
          column-gap: 50px;
          row-gap: 5px;
          justify-content: center;
          .serviceDiv {
            .serviceHead {
              background: #4698fe;
              padding: 10px 15px;
              font-size: 1.8rem;
              font-weight: 600;
              margin-bottom: 10px;
              border-radius: 2px;
              cursor: pointer;
            }
            .serviceList {
              padding-left: 2px;
              display: none;
              .serviceListItem {
                color: #ffffff;
                list-style: none;
                margin: 3px;
              }
            }
            .serviceList.active {
              animation: fadein 0.35s normal;
              margin-bottom: 8px;
            }

            @keyframes fadein {
              from {
                opacity: 0;
                margin-bottom: 0px;
              }
              to {
                opacity: 1;
                margin-bottom: 8px;
              }
            }
            #express {
              border-left: #aaaaaa solid 3px;
              padding-left: 15x;
            }
            #interior {
              border-left: #bbbbbb solid 5px;
              padding-left: 15px;
            }
            #expressPlus {
              border-left: #cccccc solid 7px;
              padding-left: 13px;
            }
            #complete {
              border-left: #dddddd solid 9px;
              padding-left: 10px;
            }
          }
        }
      }
    }

    @media (max-width: 650px) {
      flex-direction: column;
      .leftSect {
        width: 100%;
        height: 250px;
        padding-right: 0;
        .headDiv {
          margin: 0;
          justify-content: center;
          align-items: flex-end;
          .head {
            margin-top: 80px;
          }
        }
      }
      .rightSect {
        width: 100%;
        .rightSectChild {
          align-items: flex-start;
          justify-content: flex-start;
          .storyDiv {
            margin-top: 100px;
          }
        }
      }
    }


  }    
  
  @media (max-width: 500px) {
    height: 100%;
    .servicesMain{
      .leftSect{
        height: 180px;
      }
            .rightSect {
        justify-content: flex-start;
        padding-bottom: 50px;
        .rightSectChild {
          margin-top: 50px;
          width: calc(100% - 40px);
          padding: 0 20px 0 20px;
          .servicesGrid {
            width: 100%;
            .serviceDiv {
              .serviceHead {
                padding: 8px 10px;
                font-size: 1.5rem;
              }
            }
          }
        }
      }
    }
    }
}
