.home1Mother {
  height: 100vh;
  .home1Main {
    display: flex;
    flex-direction: row;
    height: 100%;

    .leftSect {
      height: 100%;
      width: 50%;
      background: #4698fe;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding-right: 30px;
      .headDiv {
        max-width: 400px;
        margin-bottom: 100px;
        display: flex;
        width: 100%;
        .head {
          text-align: end;
          color: #333;
          font-size: 4rem;
        }
      }
    }

    .rightSect {
      height: 100%;
      width: 50%;
      background: #333;
      display: flex;
      align-items: center;
      .addrSloganDiv {
        max-width: 350px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: 100px;
        padding-left: 30px;
        .addrDiv {
          .addrText {
            color: #4698fe;
            font-size: 1.8rem;

            &:after {
              content: ".";
              color: #ffffff;
            }
          }
        }
        hr {
          height: 1px;
          color: #ffffff;
          width: 100px;
          background: #ffffff;
          margin: 0;
          margin-top: 20px;
          margin-bottom: 20px;
        }
        .sloganDiv {
          .sloganText {
            text-align: start;
            color: #ffffff;
          }
        }
      }
    }


  }    
  @media (max-width: 650px) {
    height: 100%;
    .home1Main{
            flex-direction: column;
      .leftSect {
        display: flex;
        justify-content: center;
        width: 100%;
        height: 180px;
        padding-right: 0;
        .headDiv {
          margin: 0;
          justify-content: center;
          .head {
            margin-top: 80px;
            font-size: 2.5rem;
            text-align: center;
          }
        }
      }
      .rightSect {
        width: 100%;
        align-items: flex-start;
        justify-content: center;
        .addrSloganDiv {
          padding-left: 0;
          padding: 0 20px 0 20px;
          margin-top: 50px;
          align-items: center;
          .addrDiv {
            .addrText {
              font-size: 1.65rem;
              text-align: center;
              align-self: center;
            }
          }
          hr {
            align-self: center;
            width: 90%;
          }
          .sloganDiv {
            .sloganText {
            }
          }
        }
      }
    }

    }
}
